@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
* {
  scroll-behavior: smooth;
}

.header {
  width: 100%;
  cursor: pointer;
  position: fixed;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  background-color: rgba(29, 29, 29, 0.932);
  font-family: "Montserrat", sans-serif;
  font-size: 30px;
  padding-bottom: 15px;
  box-shadow: 0px 1px 5px black;
  color: white;
  z-index: 100;
}

@media (max-width: 1000px) {
  .header {
    padding-top: 15px;
    font-size: 30px;
  }
}

.app {
  background-color: rgba(0, 0, 0, 0.87);
  color: rgba(255, 255, 255, 0.274);
  padding-top: 70px;
  padding-bottom: 70px;
  background: linear-gradient(rgba(8, 8, 8, 0.5), rgba(0, 0, 0, 0.5)), url(https://mir-s3-cdn-cf.behance.net/project_modules/1400/a05a31125040963.611131c9466a7.jpg) no-repeat center center fixed;
    background-color: #202225;
    background-size: cover;
}

@media (max-width: 700px) {
  .app {
    padding-top: 80px;
  }
}

.media {
  display: flex;
  flex-direction: column;
  /* width: 200px; */
  min-height: 510px;
  padding: 5px;
  margin: 5px 0;
  border: 1px solid black;
  color: rgb(153, 150, 150);
  background-color: rgba(0, 0, 0, 0.897);
  border-radius: 10px;
  position: relative;
  font-family: "Montserrat", sans-serif;
}

.poster {
  border-radius: 10px;
  min-height: 350px;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 17px;
  padding: 8px 0;
  font-weight: bold;
  color: rgb(255, 255, 255);
}

.subTitle {
  display: flex;
  justify-content: space-between;
  padding-bottom: 3px;
  padding: 0 2px;
  padding-bottom: 3px;
}

.trending {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.pagesTitle {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-size: 2vw;
  padding: 4px;
  border-radius: 50px;
  color: white;
}

@media (max-width: 1000px) {
  .pagesTitle {
    font-size: 6.4vw;
  }
}

/* Frame */

html,
body {
  min-height: 100%;
}

#root {
  min-height: 100vh;
}
.app {
  min-height: 100vh !important;
}
.cont {
  min-height: 100%;
}

.frame {
  min-height: 75vh;
}

.main {
  height: 500px;
}

.progres {
  width: 13vw;
  padding: 0;
  margin: 0;
  justify-self: start;
  align-self: start;
}

.span-color {
  font-weight: bolder;
  color: rgb(255, 255, 255);
}

.desc {
  color: rgb(196, 196, 196);
}

.tit {
  color: gold;
}

.seasons li {
  cursor: pointer;
  color: white;
  padding: 10px;
  margin: 10px;
  font-weight: bold;
  background-color: rgb(87, 87, 87);
}

.episodes li {
  cursor: pointer;
  color: white;
  padding: 10px;
  margin: 4px;
  font-weight: bold;
  background-color: rgb(87, 87, 87);
}


.ContentModal__landscape {
  object-fit: contain;
  border-radius: 10px;
}

.ContentModal__portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
}

.tagline {
  padding-bottom: 10px;
  align-self: center;
}

.ContentModal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.ContentModal::-webkit-scrollbar {
  display: none;
}

.ContentModal__about {
  padding: 10px;
  width: 95%;
  height: 90%;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  justify-content: space-evenly;
  font-weight: 300;
}

.ContentModal__title {
  height: 20%;
  font-size: 5vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ContentModal__description {
  display: flex;
  background-color: rgb(32, 28, 28);
  height: 40%;
  overflow-y: scroll;
  padding: 30px;
  border-radius: 20px;
  scrollbar-width: thin; /* Firefox */
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;
}

.ContentModal__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .ContentModal__landscape {
    display: none;
  }
  .ContentModal__portrait {
    display: flex;
    width: 38%;
  }
  .ContentModal {
    flex-direction: row;
    justify-content: space-around;
    padding: 10px 0;
  }
  .ContentModal__about {
    width: 58%;
    padding: 0;
    height: 100%;
  }
  .ContentModal__title {
    font-size: 3.5vw;
  }
  .ContentModal__description {
    font-size: 22px;
  }
}

.inputs {
  color: rgb(255, 240, 254);
  background-color: rgba(0, 0, 0, 0.1);
}

.carouselItem {
  display: flex;
  flex-direction: column;
  object-fit: contain;
  padding: 10px;
}
.carouselItem__img {
  border-radius: 10px;
  margin-bottom: 5px;
  box-shadow: 0px 0px 5px black;
}
